import React from "react";
import {Link} from "react-router-dom";
import md5 from "js-md5";
import styles from "../styles/style.css";
export default class Login extends React.Component {
    constructor(props){
        super(props);
        this.state={
            account:"",
            pwd:""
        }
    }
    getUserName(){
        const unameVal=window.event.target.value;
        this.setState({
            account:unameVal
        });
    }
    getPwd(){
        const pwdVal=window.event.target.value;
        this.setState({
            pwd:pwdVal
        })
    }
    componentDidMount(){
        window.localStorage.setItem("account","");
        window.localStorage.setItem("email","");
    }
    LoginFetch(){
        const urlApi=`/api/login`;
        let sendData=Object.assign({},this.state);
        if(sendData.username == "" || sendData.pwd == ""){
            alert(",!");
            return false;
        }
        sendData['pwd']=md5(sendData['pwd']);
        fetch(urlApi,{
            method:"post",
            headers:{
                "Content-Type": "application/json"
            },
            body:JSON.stringify(sendData)
        }).then((result)=>{
            console.log(result);
        });
    }
    render(){
        return (
            <div class="loginBox">
                <h3></h3>
                <div class="formGroup">
                    <label>:</label>
                    <input type="text" class="formControl" onChange={this.getUserName.bind(this)} />
                </div>
                <div class="formGroup">
                    <label>:</label>
                    <input type="password" class="formControl" onChange={this.getPwd.bind(this)} />
                </div>
                <div class="btnGroup">
                    <div class="textGroup">
                        <a href="/regin"></a>
                        <Link to={"/toEmail"}></Link>
                    </div>
                    <button type="button" class="btn btn-primary" onClick={this.LoginFetch.bind(this)}></button>
                    <button type="button" class="btn btn-danger"></button>
                </div>
            </div>
        )
    }
}



